import Vue from 'vue'
import Router from 'vue-router'
import BaseLayout from '@/components/BaseLayout'
import Index from '@/pages/Index'
import Login from '@/pages/Login'
import ResetPassword from '@/pages/ResetPassword'
import WorkerInfo from '@/pages/WorkerInfo'
import OfflineTask from '@/pages/OfflineTask'
import HandleOfflineTask from '@/pages/HandleOfflineTask'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      component: BaseLayout,
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'index',
          component: Index
        },
        {
          path: '/login',
          name: 'login',
          component: Login
        },
        {
          path: '/resetPassword',
          name: 'resetPassword',
          component: ResetPassword
        },
        {
          path: '/workerInfo',
          name: 'workerInfo',
          component: WorkerInfo
        },
        {
          path: '/offlineTask',
          name: 'offlineTask',
          component: OfflineTask
        },
        {
          path: '/offlineTask/:projectID',
          name: 'handleOfflineTask',
          component: HandleOfflineTask
        }
      ]
    }
  ]
})



