<template>
  <div class="container">
    <div class="left">
      <span class="title" @click="directToIndex">
        GigaValue
      </span>
    </div>
    <div class="right">
      <el-dropdown @command="handleCommand" v-if="hasLogined">
        <span class="el-dropdown-link">
          我的账户<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <!-- <el-dropdown-item command="workerInfo">查看详情</el-dropdown-item> -->
          <!-- <el-dropdown-item command="resetPassword">修改密码</el-dropdown-item> -->
          <el-dropdown-item command="logout">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div class="login" @click="login" v-else>
        登录
      </div>
    </div>
  </div>
</template>

<script>
import Worker from '@/apis/Worker.js'

const worker = new Worker()

export default {
  computed: {
    hasLogined () {
      return this.$store.state.hasLogined
    }
  },
  methods: {
    login () {
      this.$router.push('/login')
    },
    directToIndex () {
      this.$router.push('/index')
    },
    handleCommand (command) {
      switch (command) {
        case 'workerInfo': this.handleWorkerInfo()
          break
        case 'resetPassword': this.handleResetPassword()
          break
        case 'logout': this.handleLogout()
          break
      }
    },
    handleWorkerInfo () {
      this.$router.push('/workerInfo')
    },
    handleResetPassword () {
      this.$router.push('/resetPassword')
    },
    handleLogout () {
      worker.logout()
        .then(() => {
          this.$store.commit('logout')
          this.$message.success('退出成功')
          this.$router.push('/index')
        })
        .catch(err => {
          this.$message.error(`退出失败，系统提示：${err}`)
        })
    }
  }
}
</script>

<style scoped>
.container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 70px;
  height: 60px;
  border-bottom: 1px solid #e0e0e0;
}

.title {
  font-size: 24px;
  color: #409EFF;
  cursor: pointer;
}

.login {
  color: #888;
  cursor: pointer;
  transition: color 0.2s;
}

.login:hover {
  color: #333;
}

.el-dropdown-link:hover {
  cursor: pointer;
}
</style>



