// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

Vue.config.productionTip = false
Vue.use(Vuex)
Vue.use(ElementUI)

const store = new Vuex.Store({
  state: {
    // hasLogined默认为true是为了防止用户在需要登录的页面刷新时被拦截跳转至login，会在BaseLayout组件checkLogin前commit('logout')
    // checkLogin接口暂未实现，先改回false
    hasLogined: false
  },
  mutations: {
    login (state) {
      state.hasLogined = true
    },
    logout (state) {
      state.hasLogined = false
    }
  }
})

// 无需登录即可访问的页面的路径，如果后续添加了新的无需登录可访问的页面，可将其路径加在该数组中
const unRequireAuthPath = ['/index', '/login', '/resetPassword']

router.beforeEach((to, from, next) => {
  if (unRequireAuthPath.includes(to.path)) {
    next()
  } else {
    if (store.state.hasLogined) {
      next()
    } else {
      router.app.$message('请先登录')
      next('/login')
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



