<template>
  <div class="container">
    <div class="login-container">
      <p class="title" @click="directToIndex" title="回到首页">GigaValue</p>
      <p class="slogan">加入GigaValue，成就一个极佳的你</p>
      <p class="sub-title">修改密码</p>
      <div class="operate-area">
        <el-input v-model="mobile" placeholder="请输入手机号"></el-input>
        <el-input v-model="captcha" placeholder="请输入验证码">
          <template slot="append">
            <el-button
              v-if="waitCaptcha"
              type="primary"
              disabled
              style="width: 112px">
              {{timeCount}} s
            </el-button>
            <el-button
              v-else
              type="primary"
              @click="getCaptcha"
              style="width: 112px">
              {{getBtnText}}
            </el-button>
          </template>
        </el-input>
        <el-input v-model="newPassword" placeholder="请输入新密码"></el-input>
        <div class="btn-area">
          <el-button type="primary" @click="returnLogin">想起来了</el-button>
          <el-button type="primary" @click="resetPassword">修改密码</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Worker from '@/apis/Worker.js'

const worker = new Worker()

export default {
  data () {
    return {
      mobile: '',
      captcha: '',
      newPassword: '',
      waitCaptcha: false,
      timeCount: 60,
      timeCountId: '',
      interval: 60
    }
  },
  computed: {
    getBtnText () {
      return this.firstGetCaptcha ? '发送验证码' : '重新发送'
    }
  },
  methods: {
    directToIndex () {
      this.$router.push('/index')
    },
    getCaptcha () {
      if (!this.mobile || !/^((1[3-9][0-9])+\d{8})$/.test(this.mobile)) {
        this.$message.error('手机号码有误')
        return
      }
      this.waitCaptcha = true
      this.firstGetCaptcha = false
      this.$message('验证码已发送，请注意查收')
      const data = {
        mobile: this.mobile
      }
      worker.getCaptcha(data)
        .then(res => {
          this.interval = res.data.interval
          this.timeCount = this.interval
        })
        .catch(err => {
          this.$message.error(`系统提示：${err}`)
        })
      this.startTimeCount()
    },
    startTimeCount () {
      this.timeCountId = setInterval(() => {
        if (this.timeCount === 0) {
          clearInterval(this.timeCountId)
          this.waitCaptcha = false
          this.timeCount = this.interval
          return
        }
        this.timeCount--
      }, 1000)
    },
    returnLogin () {
      if (this.$store.state.hasLogined) {
        this.$router.go(-1)
      } else {
        this.$router.push('/login')
      }
    },
    resetPassword () {
      if (!this.mobile || !/^((1[3-9][0-9])+\d{8})$/.test(this.mobile)) {
        this.$message.error('手机号码有误')
        return
      } else if (!/\d{6}/.test(this.captcha)) {
        this.$message.error('验证码有误')
        return
      } else if (!this.newPassword) {
        this.$message.error('请输入新密码')
      }
      const data = {
        mobile: this.mobile,
        captcha: this.captcha,
        new_password: this.newPassword
      }
      worker.resetPasswordByCaptcha(data)
        .then(() => {
          // 如果用户已登录，要帮用户登出，并提示用户重新登录
          if (this.$store.state.hasLogined) {
            this.$store.commit('logout')
            this.$message.success('修改密码成功，请重新登录')
          } else {
            this.$message.success('修改密码成功')
          }
          this.$router.push('/login')
        })
        .catch(err => {
          this.$message.error(`修改密码失败，系统提示：${err}`)
        })
    }
  },
  beforeDestroy () {
    clearInterval(this.timeCountId)
  }
}
</script>

<style>
.login-container {
  width: 400px;
  margin: 100px auto;
  padding: 30px 20px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
}

.title {
  width: 120px;
  margin: 0 auto 10px auto;
  font-size: 26px;
  color: #409EFF;
  cursor: pointer;
}

.slogan {
  margin-bottom: 15px;
  font-size: 22px;
  text-align: center;
  color: #409EFF;
}

.sub-title {
  margin-bottom: 15px;
  font-size: 20px;
  text-align: center;
  color: #888;
}

.operate-area {
  width: 350px;
  margin: auto;
}

.el-input {
  margin-bottom: 20px;
}

.btn-area {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.btn-area .el-button {
  width: 48%;
}
</style>



