<template>
  <div class="container">
    <i class="el-icon-back back-to-top" title="回到顶部" @click="backToTop"></i>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>离线任务</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- <div class="filter">
      <p class="filter-title">按条件查询任务</p>
      <div>
        <el-select v-model="taskType" placeholder="请选择任务类型">
          <el-option-group
            key="请选择任务类型"
            label="请选择任务类型">
            <el-option
              v-for="item in taskTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-option-group>
        </el-select>
        <el-select v-model="taskStatus" placeholder="请选择任务状态">
          <el-option-group
            key="请选择任务状态"
            label="请选择任务状态">
            <el-option
              key="0"
              label="所有"
              value="0">
            </el-option>
            <el-option
              key="1"
              label="可领取"
              value="1">
            </el-option>
            <el-option
              key="2"
              label="进行中"
              value="2">
            </el-option>
            <el-option
              key="3"
              label="已完成"
              value="3">
            </el-option>
          </el-option-group>
        </el-select>
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="searchTask">
          搜索
        </el-button>
        <el-button
          icon="el-icon-refresh"
          @click="reset">
          重置
        </el-button>
      </div>
    </div> -->
    <div class="task-list" v-loading="loading">
      <div class="task-item" v-for="item in taskItems" :key="item.pid">
        <div class="task-status" :class="getStatusClass(item.status_code)">
          <span class="task-status-text" :class="getStatusClass(item.status_code)">
            {{item.status_code | formatStatus}}
          </span>
        </div>
        <p class="task-name">{{item.name}}</p>
        <p class="task-description">任务描述：{{item.description}}</p>
        <div class="item-footer">
          <p>任务类型：离线任务</p>
          <p>奖励金额：￥ {{item.reward_money | formatMoney}}</p>
          <p>截止日期：{{item.ctime | formatTime}}</p>
          <div class="btn-area">
            <el-button
              :type="getBtnType(item.status_code)"
              @click="downloadTaskPackage(item.pid)">
              下载任务包
            </el-button>
            <el-button
              :type="getBtnType(item.status_code)"
              @click="handleClick(item.pid)">
              {{getBtnText(item.status_code)}}
            </el-button>
          </div>
        </div>
      </div>
    </div>
    <el-pagination
      layout="prev, pager, next"
      :page-count="pageCount"
      @current-change="handleCurrentChange">
    </el-pagination>
  </div>
</template>

<script>
import Project from '@/apis/Project.js'

const project = new Project()

export default {
  data () {
    return {
      taskType: '',
      taskTypeOptions: [
        {
          label: '所有',
          value: 1
        },
        {
          label: '数据标注',
          value: 2
        },
        {
          label: '数据采集',
          value: 3
        }
      ],
      taskStatus: '',
      loading: true,
      taskItems: [],
      pageCount: 0
    }
  },
  methods: {
    backToTop () {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
    getStatusClass (statusCode) {
      switch (statusCode) {
        case 1: return 'todo'
        case 100: return 'done'
        default: return 'doing'
      }
    },
    getBtnType (statusCode) {
      switch (statusCode) {
        case 1: return 'primary'
        case 100: return 'info'
        default: return 'success'
      }
    },
    getBtnText (statusCode) {
      switch (statusCode) {
        case 1: return '领取任务'
        case 100: return '任务详情'
        default: return '提交审核'
      }
    },
    handleClick (projectID) {
      this.$router.push(`/offlineTask/${projectID}`)
    },
    downloadTaskPackage (projectID) {
      let src = ''
      project.downloadTaskPackage(projectID)
        .then(res => {
          src = res.data.content[0].src
          let a = document.createElement('a')
          // 如果浏览器支持a元素的download属性，使用以下方法下载
          if ('download' in a) {
            a.href = src
            a.download = true
            document.body.appendChild(a)
            a.click()
            document.body.removeChild(a)
          } else {
            window.open(src, '_blank')
          }
        })
        .catch(err => {
          this.$message.error(`系统提示：${err}`)
        })
    },
    searchTask () {
      this.$message('该功能尚未完成，敬请期待')
    },
    reset () {
      this.taskType = ''
      this.taskStatus = ''
      this.getTaskList()
    },
    handleCurrentChange (currentPage) {
      this.getTaskList(currentPage)
    },
    getTaskList (page = 1) {
      this.loading = true
      const params = {page}
      project.getOfflineTaskList(params)
        .then(res => {
          this.taskItems = res.data.content
          this.pageCount = res.data.page_info.total_page
          this.loading = false
          window.scrollTo(0, 0)
        })
        .catch(err => {
          this.$message.error(`系统提示：${err}`)
        })
    }
  },
  filters: {
    formatStatus (value) {
      switch (value) {
        case 1: return '可领取'
        case 30: return '提交中'
        case 3: return '已提交'
        case 4: return '质检成功'
        case 5: return '质检失败'
        case 100: return '已完成'
        default: return '进行中'
      }
    },
    formatMoney (value) {
      return value / 1000
    },
    formatTime (value) {
      return value.substring(0, 10)
    }
  },
  mounted () {
    this.getTaskList()
  }
}
</script>

<style scoped>
.back-to-top {
  position: fixed;
  right: 50px;
  bottom: 100px;
  padding: 8px;
  font-size: 20px;
  color: #409eff;
  border: 1px solid #b8bcc3;
  transform: rotate(90deg);
  cursor: pointer;
  transition: all 0.2s;
}

.back-to-top:hover {
  border-color: #409eff;
}

.el-breadcrumb {
  margin: 20px 0;
}

.filter {
  margin-bottom: 30px;
}

.filter-title {
  margin-bottom: 20px;
  color: #333;
}

.filter .el-select {
  margin-right: 20px;
}

.task-list {
  min-height: 190px;
}

.task-item {
  position: relative;
  width: 1000px;
  margin: 0 auto 40px auto;
  padding: 20px 20px 20px 60px;
  border: 1px solid #e0e0e0;
}

.task-status {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 38px;
  height: 100%;
}

.task-status-text {
  position: absolute;
  top: 50%;
  left: 0;
  width: 18px;
  padding: 0px 10px;
  overflow-wrap: break-word;
  letter-spacing: 20px;
  font-size: 18px;
  color: white;
  transform: translateY(-50%);
}

.todo {
  background: #409eff;
}

.doing {
  background: #67C23A;
}

.done {
  background: #909399;
}

.task-name {
  margin: 20px 0;
  font-size: 24px;
  color: #333;
}

.task-description {
  overflow: hidden;
  width: 78%;
  margin-bottom: 20px;
  font-size: 14px;
  color: #999;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.item-footer {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  color: #333;
}

.el-pagination {
  margin-top: 30px;
  text-align: center;
}
</style>



