<template>
  <div class="container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/offlineTask' }">离线任务</el-breadcrumb-item>
      <el-breadcrumb-item>{{getBreadCrumbText}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="info-container" v-loading="loading">
      <div class="title">
        <el-button
          class="back"
          type="text"
          title="返回上一级"
          icon="el-icon-arrow-left"
          @click="backToOfflineTask">
        </el-button>
        <span class="title-content">任务详情</span>
      </div>
      <div class="task-info">
        <p class="info-item">
          <span class="info-title">
            任务编号：
          </span>
          <span class="info-content">
            {{pid}}
          </span>
        </p>
        <p class="info-item">
          <span class="info-title">
            任务名称：
          </span>
          <span class="info-content">
            {{name}}
          </span>
        </p>
        <p class="info-item">
          <span class="info-title">
            任务描述：
          </span>
          <span class="info-content">
            {{description}}
          </span>
        </p>
        <p class="info-item">
          <span class="info-title">
            任务类型：
          </span>
          <span class="info-content">
            离线任务
          </span>
        </p>
        <p class="info-item">
          <span class="info-title">
            奖励金额：
          </span>
          <span class="info-content">
            ￥ {{rewardMoney | formatMoney}}
          </span>
        </p>
        <p class="info-item">
          <span class="info-title">
            截止时间：
          </span>
          <span class="info-content">
            {{ctime | formatTime}}
          </span>
        </p>
        <p class="info-item">
          <span class="info-title">
            任务状态：
          </span>
          <span class="info-content">
            {{status_code | formatStatus}}
          </span>
        </p>
      </div>
      <div class="get-task" v-if="status_code===1">
        <el-button
          type="primary"
          @click="downloadTaskPackage">
          下载任务包
        </el-button>
        <el-button
          type="primary"
          @click="getTheOfflineTask"
          :loading="wait"
          :disabled="disabled"
          :icon="getIcon">
          {{getBtnText}}
        </el-button>
      </div>
      <div v-else-if="status_code===100">
        <p class="thanks-word">您已完成此任务，感谢您的参与！！！</p>
      </div>
      <div class="commit-task" v-else>
        <el-alert v-if="hasCommitted"
          title="您已提交，再次提交将覆盖之前的提交结果"
          type="warning"
          :closable="false">
        </el-alert>
        <div class="commit-input-area">
          <el-input class="text-input" v-model="fileName" placeholder="请输入文件名"></el-input>
          <input type="file" class="file-input" ref="fileInput">
        </div>
        <el-button
          type="success"
          plain
          @click="downloadTaskPackage">
          下载任务包
        </el-button>
        <el-button
          type="success"
          @click="commitOfflineTask"
          :loading="wait"
          :disabled="disabled"
          :icon="getIcon">
          {{getBtnText}}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Project from '@/apis/Project.js'

const project = new Project()

export default {
  data () {
    return {
      loading: true,
      pid: '',
      name: '',
      description: '',
      model_name: '',
      rewardMoney: '',
      ctime: '',
      status_code: '',
      fileName: '',
      wait: false,
      disabled: false
    }
  },
  computed: {
    getBreadCrumbText () {
      switch (this.status_code) {
        case 1: return '领取任务'
        case 100: return '任务详情'
        default: return '提交审核'
      }
    },
    hasCommitted () {
      const committedCode = [3, 4, 5]
      if (committedCode.includes(this.status_code)) {
        return true
      } else {
        return false
      }
    },
    getBtnText () {
      if (this.disabled) {
        return '操作成功'
      }
      switch (this.status_code) {
        case 1: return '确认领取'
        default: return '提交审核'
      }
    },
    getIcon () {
      return this.disabled ? 'el-icon-check' : ''
    }
  },
  methods: {
    backToOfflineTask () {
      this.$router.go(-1)
    },
    downloadTaskPackage () {
      const projectID = this.$route.params.projectID
      let src = ''
      project.downloadTaskPackage(projectID)
        .then(res => {
          src = res.data.content[0].src
          let a = document.createElement('a')
          // 如果浏览器支持a元素的download属性，使用以下方法下载
          if ('download' in a) {
            a.href = src
            a.download = true
            document.body.appendChild(a)
            a.click()
            document.body.removeChild(a)
          } else {
            window.open(src, '_blank')
          }
        })
        .catch(err => {
          this.$message.error(`系统提示：${err}`)
        })
    },
    getTheOfflineTask () {
      this.wait = true
      const projectID = this.$route.params.projectID
      project.getTheOfflineTask(projectID)
        .then(() => {
          this.$message.success('领取任务成功')
          this.wait = false
          this.disabled = true
        })
        .catch(err => {
          this.$message.error(`领取任务失败，系统提示：${err}`)
          this.wait = false
        })
    },
    commitOfflineTask () {
      if (this.fileName === '') {
        this.$message.error('文件名有误')
        return
      } else if (this.$refs.fileInput.value === '') {
        this.$message.error('请选择上传的文件')
        return
      }
      this.wait = true
      const projectID = this.$route.params.projectID
      let src = ''
      project.commitGetPath(projectID, this.fileName)
        .then(res => {
          let url = res.data.content[0].src
          let srcStart = url.indexOf('com') + 3
          src = url.substring(srcStart)
          return this.$refs.fileInput.files[0]
        })
        .then(fileData => {
          return project.commitPutFile(src, fileData)
        })
        .then(() => {
          return project.checkCommit(projectID)
        })
        .then(() => {
          this.$message.success('提交成功')
          const projectID = this.$route.params.projectID
          this.loading = true
          // 提交成功，刷新状态
          return project.getOfflineTaskDetail(projectID)
        })
        .then(res => {
          this.pid = res.data.content[0].pid
          this.name = res.data.content[0].name
          this.description = res.data.content[0].description
          this.rewardMoney = res.data.content[0].reward_money
          this.ctime = res.data.content[0].ctime
          this.status_code = res.data.content[0].status_code
          this.loading = false
          this.wait = false
        })
        .catch((err) => {
          this.$message.error(`提交失败，系统提示：${err}`)
          this.wait = false
        })
    }
  },
  filters: {
    formatMoney (value) {
      return value / 1000
    },
    formatTime (value) {
      return value.substring(0, 10)
    },
    formatStatus (value) {
      switch (value) {
        case 1: return '可领取'
        case 30: return '提交中'
        case 3: return '已提交'
        case 4: return '质检成功'
        case 5: return '质检失败'
        case 100: return '已完成'
        default: return '进行中'
      }
    }
  },
  mounted () {
    const projectID = this.$route.params.projectID
    project.getOfflineTaskDetail(projectID)
      .then(res => {
        this.pid = res.data.content[0].pid
        this.name = res.data.content[0].name
        this.description = res.data.content[0].description
        this.rewardMoney = res.data.content[0].reward_money
        this.ctime = res.data.content[0].ctime
        this.status_code = res.data.content[0].status_code
        this.loading = false
      })
      .catch(err => {
        this.$message.error(`系统提示：${err}`)
      })
  }
}
</script>

<style scoped>
.el-breadcrumb {
  margin-bottom: 20px;
}

.title {
  padding-bottom: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  color: #333;
  border-bottom: 1px solid #e0e0e0;
}

.back {
  vertical-align: middle;
  font-size: 20px;
  margin-right: 10px;
}

.title-content {
  vertical-align: middle;
}

.info-item {
  margin-bottom: 30px;
}

.info-title {
  color: #888;
}

.info-content {
  color: #333;
}

.el-alert {
  margin-bottom: 20px;
}

.text-input {
  width: 280px;
  margin-right: 10px;
}

.file-input {
  cursor: pointer;
}

.file-input:focus {
  outline: none;
}

.commit-input-area {
  margin-bottom: 20px;
}

.thanks-word {
  font-size: 18px;
}
</style>



