<template>
  <div class="container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="worker-info-container" v-loading="loading">
      <p class="title">用户信息</p>
      <div class="worker-info">
        <p class="worker-info-item">
          <span class="info-title">UID：</span>
          <span class="info-text">{{workerInfo.uid}}</span>
        </p>
        <p class="worker-info-item">
          <span class="info-title">姓名：</span>
          <span class="info-text">{{workerInfo.realname}}</span>
        </p>
        <p class="worker-info-item">
          <span class="info-title">手机：</span>
          <span class="info-text">{{workerInfo.mobile}}</span>
        </p>
        <p class="worker-info-item">
          <span class="info-title">学历：</span>
          <span class="info-text">{{workerInfo.academic}}</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Worker from '@/apis/Worker.js'

const worker = new Worker()

export default {
  data () {
    return {
      loading: true,
      workerInfo: {
        uid: '',
        realname: '',
        mobile: '',
        academic: ''
      }
    }
  },
  mounted () {
    worker.getWorkerInfo()
      .then(res => {
        this.workerInfo = res.data
        this.loading = false
      })
      .catch(err => {
        this.$message.error(`系统提示：${err}`)
      })
  }
}
</script>

<style scoped>
.worker-info-container {
  width: 350px;
  margin: 50px auto;
  padding: 30px 30px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
}

.title {
  padding-bottom: 30px;
  text-align: center;
  font-size: 20px;
  color: #888;
  border-bottom: 1px solid #e0e0e0;
}

.worker-info-item {
  margin-top: 30px;
}

.info-title {
  color: #888;
}

.info-text {
  color: #333;
}
</style>



