<template>
  <div class="container">
    <div class="login-container">
      <p class="title" @click="directToIndex" title="回到首页">GigaValue</p>
      <p class="slogan">{{register ? '注册' : '登录'}}GigaValue，成就一个极佳的你</p>
      <div class="login-form" v-if="isLoginByCaptcha">
        <el-input v-model="mobile" placeholder="请输入手机号"></el-input>
        <el-input v-model="captcha" placeholder="请输入验证码">
          <template slot="append">
            <el-button
              v-if="waitCaptcha"
              type="primary"
              disabled
              style="width: 112px">
              {{timeCount}} s
            </el-button>
            <el-button
              v-else
              type="primary"
              @click="getCaptcha"
              style="width: 112px">
              {{getBtnText}}
            </el-button>
          </template>
        </el-input>
        <div class="action">
          <span class="change-login-type" @click="changeLoginType">密码登录</span>
          <span class="register" @click="registerWorker" v-if="!register">注册用户</span>
        </div>
        <el-button class="login-btn" type="primary" @click="LoginByCaptcha">登录</el-button>
      </div>
      <div class="login-form" v-else>
        <el-input v-model="mobile" placeholder="请输入手机号"></el-input>
        <el-input type="password" v-model="password" placeholder="请输入密码"></el-input>
        <!-- <div class="action">
          <span class="change-login-type" @click="changeLoginType">手机验证码登录</span>
          <span class="forget-password" @click="forgetPassward">忘记密码</span>
        </div> -->
        <el-button class="login-btn" type="primary" @click="LoginByPassword">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Worker from '@/apis/Worker.js'

const worker = new Worker()

export default {
  data () {
    return {
      isLoginByCaptcha: false,
      register: false,
      mobile: '',
      captcha: '',
      password: '',
      waitCaptcha: false,
      timeCount: 60,
      timeCountId: '',
      interval: 60,
      firstGetCaptcha: true
    }
  },
  computed: {
    getBtnText () {
      return this.firstGetCaptcha ? '发送验证码' : '重新发送'
    }
  },
  methods: {
    directToIndex () {
      this.$router.push('/index')
    },
    getCaptcha () {
      if (!this.mobile || !/^((1[3-9][0-9])+\d{8})$/.test(this.mobile)) {
        this.$message.error('手机号码有误')
        return
      }
      this.waitCaptcha = true
      this.firstGetCaptcha = false
      this.$message('验证码已发送，请注意查收')
      const data = {
        mobile: this.mobile
      }
      worker.getCaptcha(data)
        .then(res => {
          this.interval = res.data.interval
          this.timeCount = this.interval
        })
        .catch(err => {
          this.$message.error(`系统提示：${err}`)
        })
      this.startTimeCount()
    },
    startTimeCount () {
      this.timeCountId = setInterval(() => {
        if (this.timeCount === 0) {
          clearInterval(this.timeCountId)
          this.waitCaptcha = false
          this.timeCount = this.interval
          return
        }
        this.timeCount--
      }, 1000)
    },
    changeLoginType () {
      this.isLoginByCaptcha = !this.isLoginByCaptcha
      this.register = false
    },
    registerWorker () {
      this.register = true
    },
    forgetPassward () {
      this.$router.push('/resetPassword')
    },
    LoginByCaptcha () {
      if (!this.mobile || !/^((1[3-9][0-9])+\d{8})$/.test(this.mobile)) {
        this.$message.error('手机号码有误')
        return
      } else if (!/\d{6}/.test(this.captcha)) {
        this.$message.error('验证码有误')
        return
      }
      const data = {
        mobile: this.mobile,
        captcha: this.captcha
      }
      worker.loginByCaptcha(data)
        .then(() => {
          this.$message.success('登录成功')
          this.$store.commit('login')
          this.$router.push('/index')
        })
        .catch(err => {
          this.$message.error('登录失败，系统提示：' + err)
        })
    },
    LoginByPassword () {
      if (!this.mobile || !/^((1[3-9][0-9])+\d{8})$/.test(this.mobile)) {
        this.$message.error('手机号码有误')
        return
      } else if (!this.password) {
        this.$message.error('请输入密码')
        return
      }
      const data = {
        mobile: this.mobile,
        password: this.password
      }
      worker.loginByPassword(data)
        .then(() => {
          this.$message.success('登录成功')
          this.$store.commit('login')
          this.$router.push('/index')
        })
        .catch(err => {
          // 没有检查登录接口，客户端暂时先处理已登录状态，将已登录视为登录
          if (err === '失败：用户已登录') {
            this.$message.success('登录成功')
            this.$store.commit('login')
            this.$router.push('/index')
          } else {
            this.$message.error('登录失败，系统提示：' + err)
          }
        })
    }
  },
  beforeCreate () {
    if (this.$store.state.hasLogined) {
      this.$message.warning('您已登录，请勿重复登录')
      this.$router.go(-1)
    }
  },
  beforeDestroy () {
    clearInterval(this.timeCountId)
  }
}
</script>

<style scoped>
.login-container {
  width: 400px;
  margin: 100px auto;
  padding: 30px 20px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
}

.title {
  width: 120px;
  margin: 0 auto 10px auto;
  font-size: 26px;
  color: #409EFF;
  cursor: pointer;
}

.slogan {
  margin-bottom: 30px;
  font-size: 22px;
  text-align: center;
  color: #409EFF;
}

.login-form {
  width: 350px;
  margin: auto;
}

.el-input {
  margin-bottom: 20px;
}

.action {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.change-login-type,
.register,
.forget-password {
  font-size: 15px;
  color: #888;
  cursor: pointer;
  transition: color 0.2s;
}

.change-login-type:hover,
.register:hover,
.forget-password:hover {
  color: #333;
}

.login-btn {
  width: 100%;
  margin-top: 20px;
}
</style>



