import BaseApi from '@/apis/BaseApi.js'

export default class Project extends BaseApi {
  getOfflineTaskList (params = {}) {
    const url = '/worker/project/my'
    return this.get(url, params)
  }

  getOfflineTaskDetail (projectID) {
    const url = `/worker/project/${projectID}`
    return this.get(url)
  }

  // 领取离线任务
  getTheOfflineTask (projectID) {
    const url = `/worker/project/${projectID}/arrange`
    return this.get(url)
  }

  downloadTaskPackage (projectID) {
    const url = `/worker/project/${projectID}/download`
    return this.get(url)
  }

  commitGetPath (projectID, objectKey) {
    const url = `/worker/project/${projectID}/commit`
    const params = {
      object_key: objectKey,
      project_id: projectID
    }
    return this.get(url, params)
  }

  commitPutFile (src, data) {
    const url = '/put' + src
    return this.put(url, data)
  }

  checkCommit (projectID) {
    const url = `/worker/project/${projectID}/check_commit`
    const params = {
      project_id: projectID
    }
    return this.get(url, params)
  }
}



