<template>
  <el-menu router text-color="#888888" >
    <el-menu-item index="/index">首页</el-menu-item>
    <el-menu-item index="/offlineTask">离线任务</el-menu-item>
  </el-menu>
</template>

<script>
export default {
  methods: {
    directToOfflineTask () {
      this.$router.push('/offlineTask')
    }
  }
}
</script>

<style>
.el-menu {
  width: 250px;
  border: none;
}
</style>



