<style scoped>
.middle {
  display: flex;
  flex-flow: row nowrap;
}

.middle-left {
  flex: 0 0 auto;
  min-height: calc( 100vh - 115px );
  border-right: 1px solid #e0e0e0;
}

.middle-right {
  flex: 1 1 auto;
  padding: 20px;
}
</style>



